body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: "GOODDOG";
  src: local("GoodDog"),
    url("./assets/fonts/GoodDog/GOODDC__.TTF") format("truetype");
  font-weight: bold;
}

@font-face {
  font-family: "Puppy";
  src: local("PuppyBellies"),
    url("./assets/fonts/puppybellies/PuppyBellies.ttf") format("truetype");
}